.pagination_box p{
    font-size: 15px;
    font-weight: bold;
    margin: 0;
    
}
.pagination_box_list{
    display: flex;
    flex-direction: row;
    list-style: none;
}
.pagination_box_list li{
    border: 2px solid rgb(84, 214, 223);
    padding: 3px 8px;
    font-size: 15px;
    font-weight: bold;
    cursor: pointer;
}
.pagination_box_list{
    margin: 0;
    padding: 0;
}
.pagination_box_list li:hover{
    background-color: azure;
    color: rgb(104, 104, 220);
}
.pagination_box_list button:hover{
    background-color: rgb(69, 136, 195);
}
.pagination_box_list button{
    border: 2px solid black;
    background-color: transparent;
    border-right: none;
    font-size: 15px;
    font-weight: bold;
    cursor: pointer;
    border-radius: 0px;
    padding-right: 1rem;
    padding-left: 1rem; 
    color:rgb(223, 156, 84);
    margin:0;
}
.pagination_box_list .last{
    border-right: 2px solid black;
    border-left: none;
}
